.photos{
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 3rem 0 2rem 0; 
}

h2{
    color: #fff;
}



.photos-container{
    display:grid;
    grid-template-columns: repeat(7 1fr);
    grid-template-rows: repeat(5 1fr);
    
}


.photos-container img{
    width: 100%;
    height: 90%;
}

.photo1{
    grid-area: 2/4/4/7;
}

.photo2{
    grid-area: 1/1/2/4;
}


.photo3{
    grid-area: 3/1/5/4;
}

@media screen and (max-width:870px) {

    .photos-container{
        grid-template-columns: 1fr;
        max-width: 90%;
    }
    
}