.pricing{
    width: 100%;
    padding: 6rem 1rem;
    background-color: #322b2b;
}

.pricing-cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    margin: auto;
    max-width: 1140px;
}

.card{
    border: 1px solid #fff;
    color: #eee;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.card:hover{
    background-color: #343434;
}

.card h3{
    font-size: 1.4rem;
    padding: 1rem;
}

.card p{
    padding: 10px 0;
}

.bar{
    border-bottom: 1px solid #eee;
    margin: 1.5rem 0;
    width: 100%;
    display: block;
}

.btc{
    font-size: 3rem;
    padding: 1.4rem 0;
    font-weight: 600;
}

.card .btn{
    width: 100%;
    margin: 1.2rem 0;
    display: block;
}

@media screen and (max-width:890px) {

    .pricing-cards{
        grid-template-columns: 1fr;
        max-width: 90%;
    }
    
}