label{
    color: #fff;
    margin-bottom: 0.7rem;
}

form{
    display: flex;
    flex-direction: column;
    max-width: 600px;
    padding: 4rem 1rem;
    margin: auto;
}

form input,
form textarea{
    color: #f4f4f4;
    border-color: rgba(255, 255, 255, 0.1);
    background-color:  rgba(255, 255, 255, 0.1);
    font-size: 1.2rem;
    padding: 10px 18px;
    margin-bottom: 1rem;
}