h4{
    font-size: 1.2rem;
    padding-bottom: 0.8rem;
}

p{
    font-size: 1.1rem;
    padding-bottom: 0.8rem;
}

.footer{
    width: 100%;
    padding: 5rem 0;
    background-color: #28272be6;
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left{
    max-width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
}

.location{
    display: flex;
    line-height: 20px;
}
.left p{
    padding-bottom: 0.8rem;
}

.right{
    max-width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
}

.social{
    margin-top: 1rem;
}

@media screen and (max-width:890px)
{
    .footer-container{
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(2, 1fr);
    }
}