.mask{
    position: relative;
    width: 100%;
    height: 100vh;
}

.mask .hero-image{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100vh;
    object-fit: cover;
}
.mask::after{
    content: "";
    background-color: #000;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.4;
} 

.hero{
    width: 100%;
    height: 100%;
}

.hero .hero-details{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
}

.hero .hero-details h1{
    font-size: 4rem;
    padding: 2rem 0 ;
}

.hero .hero-details p{
    font-size: 1.5rem;
    font-weight: 200;
    text-transform: uppercase;
}

.hero-details .btn,
.hero-details .btn-light{
    margin: 1rem 0.2rem;
}

@media screen and (max-width:640px){
    .hero .hero-details h1{
        font-size: 3rem;
    }

    .mask{
        height: 60vh;
    }
    .mask .hero-image{
        height: 60vh;
    }
    
    .hero .hero-details p{
        font-size: 1rem;
    }
}
