.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;  
}

.project-cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    margin: auto;
    max-width: 1140px;
    padding-bottom: 5rem;
}

.pro-card{
    padding: 1.2rem 1rem;
    background-color: #1b1e2c;
}

.pro-card img{
    width: 100%;
}

.pro-title{
    color: #fff;
    padding: 1rem 0;
}

.pro-details{
    font-size: 1.1rem;
    text-align: justify;
    padding-bottom: 1rem;
}

.pro-btns{
    display: flex;
    justify-content: space-between;
    padding: 0.8rem 0;
}

.pro-btns .btn{
    padding: 0.8rem 1rem;
}

@media screen and (max-width:870px) {

    .project-cards{
        grid-template-columns: 1fr;
        max-width: 90%;
    }
    
}