@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;0,900;1,300&family=Ubuntu:wght@700&display=swap');

*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-family: 'Ubuntu', sans-serif;
}

body{
  background-color: rgb(16, 16, 31);
}

h1, h4, p, a{
  color: #fff;
  text-decoration: none;
}

.btn{
  padding: 12px 32px;
  border: 1px solid #fff;
  background: yellow;
  color: #232323;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  text-transform: uppercase;

}

.btn-light{
  padding: 12px 32px;
  border: 1px solid #fff;
  background: transparent;
  color: #fff;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  text-transform: uppercase;

}

.btn:hover, .btn-light:hover{
  background-color: #b0abab;
  color: #fff;
  transition: 0.3s;
}

@media (max-width:890px){
  body{
    padding: 0;
  }
}