.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 100%;
    height: 100px;
    background-color: #4b187e00;
    position: fixed;
    z-index: 999;

    /* box-shadow: 6px 4px rgba(45, 45, 55, 0.322); */
    margin-bottom: 1.5rem;
}

.header-bg{
    background-color: #412261;
}

.logo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #fff;
}

.nav-menubar{
    display: none;
}
.nav-items{
    display: flex;
}

.nav-items li{
    list-style: none;
}

.nav-items li a{
    font-size: 1.2rem;
    padding: 0 1rem;
    font-weight: 500;
    text-decoration: none;
}

.nav-items li a:hover{
    text-decoration: underline;
    color: rgb(243, 248, 104);
}

@media (max-width:890px){
.nav-menubar {
    display: flex;
}

    .nav-items{
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 10px 15px;
      width: 100%;
      position: absolute;
      top: 0;
      left: -100%;
      height: 100vh;
      z-index: -3;
      background: #0b0c12d5;
      transition: 2s;
    }

    .nav-items.active{
        left: 0;
    }
    .nav-items li{
        padding: 1rem 0;
    }

    .nav-items a{
        font-size: 1.8rem;
        font-weight: 600;
    }
  }