
.hero-img{
    width: 100%;
    height: 90vh;
    /* background-color: #000; */
    position: relative;
}

.hero-img h1{
    font-size: 2.4rem;
}

.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}

.hero-img::before{
    content: "";
    background: url('../Assets/2Desktop.jpeg');
    background-position: center top;
    background-size: cover;
    background-repeat: no-repeat;
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 0.5;
}

.heading{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    flex-direction: column;
}

@media screen and (max-width:890px) {

    .hero-img h1{
    font-size: 2rem;
}
.hero-img{
    height: 50vh;
}
}
